<template>
<div class="home">
	<img src="@/assets/face.png" id="face" />
	<h1>Hi! I'm Gabriel Wong.</h1>
	<p>
		I study AI and business analytics at the<br>Singapore University of Technology and Design.
	</p>

	<div id="icon-bar">
		<face-icon 
			icon="mdi-github-circle"
			link="https://github.com/gabrielwong159" />
		<face-icon 
			icon="mdi-linkedin-box"
			link="https://linkedin.com/in/gabrielwong159" />
		<face-icon
			icon="mdi-email"
			link="mailto:gabriel_wong@mymail.sutd.edu.sg" />
	</div>
</div>
</template>

<script>
import Vue from 'vue';

Vue.component('face-icon', {
	props: ['icon', 'link'],
	template: `
		<a :href="link" target="_blank">
			<i class="mdi" :class="icon" />
		</a>
	`,
});

export default {
	name: 'Home',
};
</script>

<style>
h1 {
	font-weight: 350;
}

p {
	width: 35%;
	margin: 10px auto 20px auto;
}

.mdi {
	color: #2c3e50;
	font: normal normal normal 28px/1 "Material Design Icons";
}

.mdi:hover {
	color: #808080;
}

img#face {
	width: 256px;
	height: 256px;
}
</style>



